*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  font-family: sans-serif;
  background-image: linear-gradient(to top, #accbee 100%, #e7f0fd 100%);
}
.container{
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  width: 600px;
  border-radius: 10px;
  margin: 10% auto 0;
  padding: 30px;
}
.app h1{
  border-bottom: 1px solid #333;
  padding-bottom: 30px;
  margin-bottom: 20px;
  font-size: 26px;
}
.app h2{
  font-size: 18px;
  margin-bottom: 20px;
}
.btn-container .btn{
  background: #fff;
  color: #000;
  width: 100%;
  border: 1px solid #333;
  outline: none;
  cursor: pointer;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 3px;
  text-align: left;
}
.btn-container .btn:not([disabled]):hover{
  background: #000;
  color: #fff;
  transition: 0.15s;
}
.correct.btn{
  background: lightgreen;
}
.incorrect.btn{
  background: lightcoral;
}
.btn:disabled{
  cursor: not-allowed;
}
.result{
  width: 210px;
  line-height: 50px;
  color: #353f4b;
}